import React from 'react';
import styled from 'styled-components';
import calendar from '../../img/calendar.svg';
import cart from '../../img/cart.svg';
import Fade from 'react-reveal/Fade';
import Wave from './WaveComponent';
import { FaArrowCircleDown } from 'react-icons/fa'

const Container = styled.div`
    width: 100%;
    min-height: 50vh;
    background: #2a2a2a;
    position: relative;
    margin: 0 ;
    /* margin-top: -10px; */
    @media (min-width: 768px) {
        min-height: 40vh;
    }
`

const Titulo = styled.h1`
    font-size: 35px;
    font-weight: 900;
    text-align: left;
    margin-left: 10%;
    margin-top: -10px; 
    /* border-bottom: solid 4px #98ded9; */
    border-bottom: solid 4px #97BB2B;
    /* color: #161d6f; */
    /* color: #B0DB31; */
    color: #F3F3F3;
    width: 50%;
    max-width: 200px;
    @media (min-width: 768px) {
        /* margin-top: -100px; */
    }
`

const TituloServicio = styled.h2`
    color: #2e2e2e;
`

const Toolbar = styled.div`
    width: 100%;
    height: 100px;
`

const Image = styled.img`
    width: 150px;
`

const ContainerServicio = styled.div`
    min-height: 380px;
    margin: 20px;
    padding: 17px;
    background: #F3F3F3;
    /* background: red; */
    border-radius: 20px;
    filter: drop-shadow(5px 6px 8px rgba(0, 0, 0, 1));
    @media (min-width: 768px) {
        min-height: 350px;
        padding: 15px;
    }
    
`

const Services = styled.div`
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    height: auto;
    margin-top: 30px;
    flex-wrap: wrap;
`
const Texto = styled.p`
    font-size: 15px;
    font-weight: 400;
    /* color: #0ba288; */
    color: #545454;
    width: auto;
    margin: 30px 10px;
`

const Link = styled.a`
    text-decoration: none;
    padding: 10px 30px;
    border-radius: 25px;
    border: solid 2px #B0DB31;
    transition: all .3s;
    color: #B0DB31;
    /* font-weight: 600; */
    margin: 40px 0;
    margin-bottom: 20px;
    &:hover {
        background: #B0DB31;
        color: white;
        transition: all .3s;
    }
`
const ContainerImage = styled.div`
    width: auto;
    height: 130px;
`

const SVG = styled.svg`
    margin-top: -2px;
    /* filter: drop-shadow(2px 12px 5px rgba(0, 0, 0, 0.2)); */

`

const ContainerArrow = styled.div`
    text-align: center;
    width: 100%;
    height: auto;
    position: absolute;
    bottom: 15%;
    z-index: 1;
    /* background: red; */
`

const Info = styled.p`
    font-weight: 700;
    text-align: center;
    /* color: #0ba288; */
    color: #2e2e2e;

`


const ServiciosComponent = () => {
    return ( 
        <>
        {/* <Toolbar  /> */}
        <div  id="services" />
        <ContainerArrow>
                    <Fade bottom>
                        <a href="#services" style={{textDecoration: "none", color: "#2e2e2e"}}>
                        <Info>Más información</Info>
                            <FaArrowCircleDown className="arrowDown" />
                        </a>
                    </Fade>
        </ContainerArrow>
        <Wave/>
        <Container>
            <Titulo>
                <Fade top>
                    Servicios
                </Fade>
            </Titulo>
            <Services>
                <Fade >
                    <ContainerServicio>
                        <ContainerImage>
                            <Image src={calendar} />
                        </ContainerImage>
                        <TituloServicio>Sistemas de turnos</TituloServicio>
                        <Texto>
                            Posibilidad de solicitud de turnos online para tus clientes.
                        </Texto>
                        <Texto>
                            Integración con terminal de autoconsulta, TV y llamador.
                        </Texto>
                        <Link href="https://www.soloturnos.com/index.htm" target="_blank">Ver más</Link>
                    </ContainerServicio>
                </Fade>
                <Fade >
                    <ContainerServicio>
                        <ContainerImage>
                            <Image src={cart} />
                        </ContainerImage>
                        <TituloServicio>E-commerce</TituloServicio>
                        <Texto>
                            Incluye gestión de productos por talle y color
                        </Texto>
                        <Texto>
                            Ideal para vender y promocionar tus productos online.
                        </Texto>
                        <Link href="https://www.geco.com.ar/ecommerce.php" target="_blank">Ver más</Link>
                    </ContainerServicio>
                </Fade>
            </Services>
        </Container>
        <SVG xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
            <path fill="#2a2a2a" fill-opacity="1" d="M0,128L80,149.3C160,171,320,213,480,213.3C640,213,800,171,960,170.7C1120,171,1280,213,1360,234.7L1440,256L1440,0L1360,0C1280,0,1120,0,960,0C800,0,640,0,480,0C320,0,160,0,80,0L0,0Z"></path>
        </SVG>
        </>
    );
}
 
export default ServiciosComponent;