import React, { useState } from 'react';
import styled from 'styled-components';
import geco from '../img/geco.png';
import geco4x4 from '../img/geco4x4.png';
import {Fade} from 'react-reveal';
// import { Link } from 'react-router-dom';
import Drawer from './Drawer'; 

const Navbar = styled.header`
    width: 100%;
    position: fixed; 
    top: -1px;
    left: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    z-index: 2;
    @media (max-width: 768px) {
        justify-content: flex-start;
    }
`

const ContainerLogo = styled.div`
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 200px;
    
`

const Logo = styled.img`
    width: 200px;
    margin-top: 20px;
    margin-left: 30px;
`

const Logo4x4 = styled.img`
    width: 60px;
    margin-top: 20px;
    margin-left: 50px;
`

const ContainerLinks = styled.div`
    width: 500px;
    margin-right: 40px;
    display: flex;
    justify-content: space-between;
    color: red;
    @media (max-width: 768px) {
        display: none;
    }
`

const ContainerMenu = styled.div`
    width: auto;
    height: auto;
    color: red;
    @media (min-width: 1047px){
        display: none
    }
`

const Link = styled.a`
    
`

const Header = () => {

    const [navbar, setNavbar] = useState(false);
    const [logo, setLogo] = useState(false);

    const changeScroll = () => {
        if(window.scrollY >= 80){
            setNavbar(true);
            setLogo(true)
        } else {
            setNavbar(false);
            setLogo(false)
        }
    }

    window.addEventListener('scroll', changeScroll)    

    return ( 
        <Navbar className={(navbar) ? "headerActive" :"header"}>
            <Link href="/geotech">
            {(logo)? 
            <Fade top>
                <ContainerLogo><Logo4x4 src={geco4x4} /></ContainerLogo>
            </Fade>
            : 
            <Fade top>
                <ContainerLogo><Logo src={geco} /></ContainerLogo>
            </Fade>}
            </Link>
            {/* <Fade top>
                <Logo  geco4x4 : geco} />
            </Fade> */}
            <Fade top>
                <ContainerLinks>
                    <Link className="link" id="link1" href="/geotech/">Home</Link>
                    <Link className="link"  href="/geotech/servicios">Servicios</Link>
                    <Link className="link" href="/geotech/clientes" >Clientes</Link>
                    <Link className="link" href="/geotech/contacto" >Contacto</Link>
                </ContainerLinks>
            </Fade>
            <ContainerMenu>
                    <Drawer />
                {/* <Fade top>
                </Fade> */}
            </ContainerMenu>
        </Navbar>
     );
}
 
export default Header;