import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import Fade from 'react-reveal/Fade';
import { Link, BrowserRouter as Router, Route } from "react-router-dom";

const Container = styled.div`
    width: 100%;
    min-height: 100vh;
`

const Titulo = styled.h1`
    font-size: 35px;
    font-weight: 900;
    text-align: left;
    margin-left: 10%;
    /* border-bottom: solid 4px #98ded9;
    color: #161d6f; */
    border-bottom: solid 4px #B0DB31;
    color: #2e2e2e;
    max-width: 200px;
`

const Cliente = styled.div`
    display: flex;
    flex-direction: column;
    width: 300px;
    margin: 0 20px;
    /* min-width: 300px; */
    height: 300px; 
    /* margin: 20px; */
    background: #F3F3F3;
    /* background: red; */
    /* border: 1px solid black; */
    box-shadow: 6px 5px 6px 3px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    overflow: hidden;
    @media (max-width: 768px) {
        min-width: 250px;
        width: 300px;
        margin: 0 20px;
    }
`

const NombreCliente = styled.h2`
    font-size: 20px;
    position: relative;
    top: 150px;
    left: 1px;
    color: #161d6f;
`

const ContainerClientes = styled.div`
    display: flex;
    width: 90%;
    /* background: blue; */
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    
`

const ButtonVerMas = styled.button`
    all: unset;
    border: 2px solid #00CC0F;
    font-weight: 700;
    border-radius: 20px;
    padding: 6px;
    font-size: 20px;
    position: relative;
    top: 140px;
    left: 1%;
    color: #00CC0F;
    transition: all .3s;
    &:hover {
        background: #00CC0F;
        color: white;
        transition: all .3s;
    }
`

const ClienteInfo = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 60vh;
    margin-top: 120px;
`

const ContainerCliente = styled.div`
    display: flex;
    @media (max-width: 1024px) {
        flex-direction: column;
    }
    /* background: blue; */

    /* background: red; */
    /* margin: 0 auto ; */
    /* width: 30%; */
`

const TituloClienteInfo = styled.h1`
    font-size: 35px;
    font-weight: 900;
    padding-left: 5%;
    text-align: left;
    width: 95%;
    color: #B0DB31;
    text-align: start;

    /* border-bottom: solid 4px #98ded9; */
    /* width: 20%; */
    /* background: red; */
`

const ContainerImageClienteInfo = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 5%;
    margin-right: 10px;
    width: 45%;
    height: 300px;
    background: white;
    border-radius: 20px;
    filter: drop-shadow(3px 3px 3px gray);
    @media (max-width: 1024px) {
        width: 90%;
        height: 400px;
        margin: 0 auto;
    }
`

const ContainerClienteInfo = styled.div`
    width: 50%;
    margin-left: 10px;
    margin-right: 5%;
    /* background: red; */
    @media (max-width: 1024px) {
        width: 90%;
        margin: 10px auto;
    }
`

const TituloCliente = styled.h2`
    text-align: start;
    color: #5B5B5B;
`

const PersonPage = ({ match }) => {
    const {
      params: { id },
    } = match;
    
    const [isLoading, setIsLoading] = useState(true);
    const [data, setData] = useState();
  
    useEffect(() => {
      fetch(`https://crm.cingodesarrollos.com.ar/api/cliente/${id}`, {})
        .then((res) => res.json())
        .then((response) => {
          setData(response.cliente);
          setIsLoading(false);
          // console.log(`https://crm.cingodesarrollos.com.ar/api/cliente/${id}`);
          // console.log(prov+"hola")
        })
        .catch((error) => console.log(error));
    }, [id]);
  
     console.log(data)
  
    // const stringToHTML = data.cliente.descripcion
    // const {descripcion} = data;
    // const doc = new DOMParser().parseFromString(descripcion, "text/xml");
  
  
    return (
      <>
        {!isLoading && (
          <ClienteInfo>
              {/* <Link to="/geotech/clientes/">Inicio</Link> */}
              <TituloClienteInfo>{data.nombreDeFantasia}</TituloClienteInfo>
              <ContainerCliente>
                  <ContainerImageClienteInfo>Foto</ContainerImageClienteInfo>
                  <ContainerClienteInfo>
                  <TituloCliente>{data.titulo}</TituloCliente>
                  <h4>{data.descripcion}</h4>
                  </ContainerClienteInfo>
              </ContainerCliente>
          </ClienteInfo>
        )}
      </>
    );
  };

const ListarClientes = () => {
    const [isLoading, setIsLoading] = useState(true);
    const [data, setData] = useState();
    // console.log(data)
  
    useEffect(() => {
      fetch("https://crm.cingodesarrollos.com.ar/api/cliente/list", {})
        .then((res) => res.json())
        .then((response) => {
          setData(response.clientes);
          setIsLoading(false);
        })
        .catch((error) => console.log(error));
        
    }, []);
    
    // for (let i = 0; i < data.length; i++) {
    //     const arr = []
    //     while(i <10) {
    //         const dato = data[i];
    //         arr.push(dato);
    //     }
    //     console.log(arr)
    // }

    return (
        <>
            {!isLoading &&
            data.map((cliente, index) => {
                while(index < 9){
                    return (
                        <Fade>
                            <Link to={`/geotech/clientes/${cliente.id}`} className="link">
                                <Cliente key={index}>
                                    <NombreCliente>
                                        {cliente.nombre}
                                    </NombreCliente>
                                </Cliente>
                            </Link>
                        </Fade>
                    );
                }
            })}
        </>
    );
  };


const ClientesComponent = () => {
    return ( 
        <>
        {/* <Container>
            <Fade top>
                <Titulo>TRABAJOS DESTACADOS</Titulo>
            </Fade>
            <ContainerTrabajos>
            </ContainerTrabajos>
        </Container> */}
        <Route exact path="/geotech/" >
            <Container>
                <Titulo><Fade top>Clientes</Fade></Titulo>
                <ContainerClientes>
                    <ListarClientes />
                </ContainerClientes>
            </Container>
        </Route>
        <Route path="/geotech/clientes/:id" component={PersonPage} />
        </>
    );
}
 
export default ClientesComponent;