import React from 'react';
import styled from 'styled-components';

const Container = styled.form`
    display: flex;
    align-items: center;
    /* justify-content: center; */
    flex-direction: column;
    width: 50%;
    height: 70vh;
    margin-top: 170px;
    text-align: center;
    @media (max-width: 1024px) {
        width: 100%;
        height: auto;
        margin-top: 50px;
        /* background: red; */
    }
    /* background: red; */
`

const ContainerInput = styled.div`
    background: white;
    margin: 10px 0;
    padding: 10px 10px;
    width: 70%;
    max-width: 400px;
    border-radius: 8px;
    border: 1px solid #D8D8D8;
    @media (max-width: 1024px) {
        width: 90%;
    }
    /* filter: drop-shadow(4px 6px 6px gray); */
`

const ContainerTF = styled.div`
    background: white;
    margin: 10px 0;
    padding: 10px 10px;
    width: 70%;
    max-width: 400px;
    border-radius: 8px;
    border: 1px solid #D8D8D8;
    @media (max-width: 1024px) {
        width: 90%;
    }

    /* filter: drop-shadow(4px 6px 6px gray); */
`

const Input = styled.input`
    all: unset; 
    text-align: start;
    width: 100%;
    color: #2e2e2e;
    /* background: blue; */
`

const TextField = styled.textarea`
    /* all: unset; */
    border: none;
    text-align: start;
    width: 100%;
    font-size: 16px;
    color: #2e2e2e;
    &:focus {
        outline: 0;
    }
`

const Titulo = styled.h1`
    /* font-size: 35px; */
    font-weight: 50;
    text-align: center;
    /* margin-left: 10%; */
    /* padding-right: 30px; */
    color: #2e2e2e;
    width: 100%;
    @media (max-width: 1024px) {
        text-align: center;
    }
`

const ContainerButton = styled.div`
    display: flex;
    width: 100%;
    max-width: 420px;
    @media (max-width: 1024px) {
        width: 90%;
    }
    /* background: blue; */
    /* height: 100px; */
`
const Submit = styled.input`
    all: unset;
    /* margin-left: 10px; */
    padding: 10px 30px;
    background: white;
    border: 2px solid #b0db31;
    color: #b0db31;
    border-radius: 25px;
    transition: all .3s;
    &:hover {
        cursor: pointer;
        background: #b0db31;
        color: white;
        transition: all .3s;
    }
`

const Form = () => {
    return ( 
        <Container method="POST">       
            <Titulo>Te interesa una demo? Escribinos!</Titulo>         
            <ContainerInput><Input placeholder="Nombre" type="text" className="input"/></ContainerInput>
            <ContainerInput><Input placeholder="E-Mail" type="email" className="input"/></ContainerInput>
            <ContainerInput><Input placeholder="Teléfono" type="text" className="input"/></ContainerInput>
            <ContainerTF><TextField placeholder="Mensaje" cols="50" rows="10" className="input"/></ContainerTF>
            <ContainerButton><Submit type="submit" /></ContainerButton>
        </Container>
    );
}
 
export default Form;