import React, { useState, useEffect } from "react";
import { Link, BrowserRouter as Router, Route } from "react-router-dom";
import styled from 'styled-components';
import { Fade } from 'react-reveal';

const Container = styled.div`
    /* display: flex; */
    /* margin-top: 120px; */
    /* flex-direction: column; */
    width: 100%;
    min-height: 100vh;
`

const Titulo = styled.h1`
    font-size: 35px;
    font-weight: 900;
    text-align: left;
    margin-left: 5%;
    margin-top: 130px;
    color: #B0DB31;
    border-bottom: solid 4px #97BB2B;
    width: 70%;
    max-width: 200px;
`

const ContainerClientes = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
`

const Cliente = styled.div`
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 250px; 
    margin: 20px;
    background-color: #f6f6f6;
    /* border: 1px solid black; */
    box-shadow: 6px 5px 6px 3px rgba(0, 0, 0, 0.1);
    border-radius: 25px;
    overflow: hidden;

`

const Image = styled.img`
    width: 100%;
`

const NombreCliente = styled.h2`
    font-size: 20px;
    position: relative;
    top: 150px;
    left: 1px;
    color: #161d6f;
`



const ContainerNombreCliente = styled.div `
    /* position: absolute; */
    width: 100%;
    height: 0;
`

const ButtonVerMas = styled.button`
    all: unset;
    border: 2px solid #00CC0F;
    font-weight: 700;
    border-radius: 20px;
    padding: 6px;
    font-size: 20px;
    position: relative;
    top: 140px;
    left: 1%;
    color: #00CC0F;
    transition: all .3s;
    &:hover {
        background: #00CC0F;
        color: white;
        transition: all .3s;
    }
`

const ClienteInfo = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: 60vh;
    margin-top: 120px;
`

const ContainerCliente = styled.div`
    display: flex;
    @media (max-width: 1024px) {
        flex-direction: column;
    }
    /* background: blue; */

    /* background: red; */
    /* margin: 0 auto ; */
    /* width: 30%; */
`

const TituloClienteInfo = styled.h1`
    font-size: 35px;
    font-weight: 900;
    padding-left: 5%;
    text-align: left;
    width: 95%;
    color: #B0DB31;
    text-align: start;

    /* border-bottom: solid 4px #98ded9; */
    /* width: 20%; */
    /* background: red; */
`

const ContainerImageClienteInfo = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 5%;
    margin-right: 10px;
    width: 45%;
    height: 300px;
    background: white;
    border-radius: 20px;
    filter: drop-shadow(3px 3px 3px gray);
    @media (max-width: 1024px) {
        width: 90%;
        height: 400px;
        margin: 0 auto;
    }
`

const ContainerClienteInfo = styled.div`
    width: 50%;
    margin-left: 10px;
    margin-right: 5%;
    /* background: red; */
    @media (max-width: 1024px) {
        width: 90%;
        margin: 10px auto;
    }
`

const TituloCliente = styled.h2`
    text-align: start;
    color: #5B5B5B;
`


const PersonPage = ({ match }) => {
  const {
    params: { id },
  } = match;
  
  const [isLoading, setIsLoading] = useState(true);
  const [data, setData] = useState();

  useEffect(() => {
    fetch(`https://crm.cingodesarrollos.com.ar/api/cliente/${id}`, {})
      .then((res) => res.json())
      .then((response) => {
        setData(response.cliente);
        setIsLoading(false);
        // console.log(`https://crm.cingodesarrollos.com.ar/api/cliente/${id}`);
        // console.log(prov+"hola")
      })
      .catch((error) => console.log(error));
  }, [id]);

   console.log(data)

  // const stringToHTML = data.cliente.descripcion
  // const {descripcion} = data;
  // const doc = new DOMParser().parseFromString(descripcion, "text/xml");


  return (
    <>
      {!isLoading && (
        <ClienteInfo>
            {/* <Link to="/geotech/clientes/">Inicio</Link> */}
            <TituloClienteInfo>{data.nombreDeFantasia}</TituloClienteInfo>
            <ContainerCliente>
                <ContainerImageClienteInfo>Foto</ContainerImageClienteInfo>
                <ContainerClienteInfo>
                <TituloCliente>{data.titulo}</TituloCliente>
                <h4>{data.descripcion}</h4>
                </ContainerClienteInfo>
            </ContainerCliente>
        </ClienteInfo>
      )}
    </>
  );
};

const ListarClientes = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [data, setData] = useState();
  // console.log(data)

  useEffect(() => {
    fetch("https://crm.cingodesarrollos.com.ar/api/cliente/list", {})
      .then((res) => res.json())
      .then((response) => {
        setData(response.clientes);
        setIsLoading(false);
      })
      .catch((error) => console.log(error));
  }, []);

  return (
    <>
      {!isLoading &&
        data.map((cliente, index) => {
          return (
            <Fade>
            <Link to={`/geotech/clientes/${cliente.id}`} className="link">
                <Cliente key={index}>
                    <NombreCliente>
                        {cliente.nombre}
                    </NombreCliente>
                </Cliente>
            </Link>
            </Fade>
          );
        })}
    </>
  );
};

const Data = () => {
  return (
    <>
    <Route exact path="/geotech/clientes/" >
        <Container>
            <Titulo><Fade top>CLIENTES</Fade></Titulo>
            <ContainerClientes>
                <ListarClientes />
            </ContainerClientes>
        </Container>
    </Route>
    <Route path="/geotech/clientes/:id" component={PersonPage} />
    </>
  );
};

export default Data;