import React from 'react';
import Wave from './WaveComponent';
import styled from 'styled-components';
import manogeco from '../../img/mano_geco.svg';
import gecomano from '../../img/gecomano.png';

import calendar from '../../img/calendar.svg';

import { FaArrowCircleDown } from 'react-icons/fa'
import {Bounce, Fade} from 'react-reveal';

const Container = styled.div`
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 100%;
    height: 93vh;
    overflow: hidden;
    z-index: 1;

    /* background: red; */
    /* position: fixed; */
    /* background: red; */
    @media (min-width: 768px){
        height: 80vh;
        /* height: auto; */
        align-items: center;
        /* background: red; */
    }
`

const Contenido = styled.div`
    width: 100%;
    /* background: beige; */
    height: 85vh;
    display: flex;
    align-items: space-evenly;
    justify-content: center;
    flex-direction: column;
    /* overflow: none; */
    @media (min-width: 1047px) {
        height: 60vh;
        width: 80%;
    }

`

const ContainerBg = styled.div`
    padding: 0;
    width: 700px;
    height: auto;
    position: absolute;
    /* top: 60px; */
    top: -30px;
    /* background: red; */
    padding: 0 0 50px 0;
    right: -40px;
    overflow: hidden;
    @media (max-width: 768px) {
        bottom: -10px;
    }
`

const Bg = styled.img`
    width: 600px;
    height: auto;
    position: relative;
    right: -350px;
    bottom: -100px;
    border: none;
    /* bottom: 19%; */
    opacity: 20%;
    filter: drop-shadow(3px 20px 6px rgba(0, 0, 0, 0.5)); 

    @media (min-width: 768px) {
        width: 1000px;
        opacity: 50%;
        /* bottom: -10px; */
        top: 0;
        right: -200px;
        /* bottom: -50%;
        right: -150px; */
    }
`

const ContainerTexto = styled.div`
    display: flex;
    flex-direction: column;
    /* align-items: center;
    justify-content: center; */
    width: 100%;
    height: auto;
    /* background: blue; */

    @media (max-width: 768px) {
        margin-bottom: 50px;
    }
`

const Texto = styled.p`
    font-size: 1.1rem;
    font-weight: 900;
    /* color: #B0DB31; */
    color: #2e2e2e;
    /* background: red; */
    /* filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));  */
    letter-spacing: 3px;
    margin: 10px 0;
    width: auto;
    z-index: 1;
    text-align: left;
    @media (min-width: 768px) {
        font-size: 2rem;
        text-align: left;
        /* margin: 10px 0; */
        /* padding-right: 300px; */
    }
`

const ContainerArrow = styled.div`
    width: auto;
    height: auto;
    position: absolute;
    bottom: 15%;
    z-index: 2;
`

const Link = styled.a`
    text-decoration: none;
    color: black;
    z-index: 2;
    position: relative;
    /* background: red; */
    text-align: center;
    width: 100%;
    height: 40px;

`

const Info = styled.p`
    font-weight: 700;
    text-align: center;
    /* color: #0ba288; */
    color: #2e2e2e;
    text-align: left;

`

const Dedicacion = styled.p`
    width: auto;
    margin: 10px 0;
    text-align: left;
    font-size: 1.1rem;
    font-weight: 900;
    letter-spacing: 3px;
    color: #2e2e2e;
    z-index: 1;
    margin: 10px 0 10px 40px;
    @media (min-width: 768px) {
        font-size: 2rem;
    }
`

const Innovacion = styled.p`
    width: auto;
    margin: 10px 0;
    text-align: left;
    font-size: 1.1rem;
    font-weight: 900;
    letter-spacing: 3px;
    color: #2e2e2e;
    z-index: 1;
    margin: 10px 0 10px 80px;
    @media (min-width: 768px) {
        font-size: 2rem;
    }
`



const HomeComponent = () => {
    return ( 
        <>
        <Container >
            <Contenido>
                <ContainerTexto>
                    <Texto><Fade left>Evolución</Fade></Texto>
                    <Dedicacion > <Fade left>Dedicación</Fade></Dedicacion>
                    <Innovacion ><Fade left>Innovación</Fade></Innovacion>
                    <Texto style={{marginTop: 30, color: "#B0DB31"}}><Fade bottom>Amamos lo que hacemos!</Fade></Texto>
                </ContainerTexto>
                {/* <img src={manogeco} style={{width: 100, height: 100}} /> */}
                {/* <Texto><Fade bottom>Innovación</Fade></Texto>
                
                <Texto><Fade>Amamos lo que hacemos!</Fade></Texto> */}
            </Contenido>
                <ContainerBg><Bg src={gecomano}/></ContainerBg>
                {/* <img src={gecomano}></img> */}
                {/* <ContainerArrow>
                    <Fade bottom>
                        <Link href="#services">
                        <Info>Más información</Info>
                            <FaArrowCircleDown className="arrowDown" /></Link>
                    </Fade>
                </ContainerArrow> */}
        </Container>
        </>
    );
}
 
export default HomeComponent;