import React from 'react';
import styled from 'styled-components';

const Container = styled.form`
    display: flex;
    align-items: flex-start;
    /* justify-content: center; */
    flex-direction: column;
    width: 40%;
    height: 70vh;
    margin: 170px 0 0 10%;
    @media (max-width: 1024px) {
        width: auto;
        height: auto;
        margin: 50px 0 0 0;
    }
    /* background: red; */
`

const Titulo = styled.h1`
    /* font-size: 35px; */
    font-weight: 50;
    text-align: left;
    /* margin-left: 10%; */
    padding-right: 30px;
    color: #2e2e2e;
    /* border-bottom: solid 4px #B0DB31; */
`


const Info = styled.p`
    /* padding-left: 40px; */
    margin-top: 5px;
    font-size: 16px;
    font-weight: 500;
    /* font-style: italic; */
    font-weight: 100;
    text-align: left;
    color: #5F6D7B;
`

const Label = styled.label`
    font-weight: 500;
    font-style: initial;
    text-align: start;
    margin-bottom: 10px;
`

const Mail = styled.a`
    font-weight: 100;
    text-decoration: none;
    color: #5F6D7B;
    &:hover {
        color: #B0DB31;
    }
`

const ContainerMapa = styled.div`
    width: 100%;
    margin-top: 30px;
    border-radius: 20px;
    overflow: hidden;
    filter: drop-shadow(2px 2px 2px gray); 
`


const Datos = () => {
    return ( 
        <Container>
            <Titulo>Contáctanos</Titulo>
            <Label>Dirección<Info> Moreno 970 Piso 5, CABA, Argentina</Info></Label>
            <Label>Teléfono<Info> +54 11-5353-5515</Info></Label>
        </Container>
    );
}
 
export default Datos;