import React from 'react';
import ClientesComponent from '../components/Home/ClientesComponent';
import HomeComponent from '../components/Home/HomeComponent';
import ServiciosComponent from '../components/Home/ServiciosComponent';
import { Parallax } from 'react-scroll-parallax';

const Home = () => {
    return ( 
        <div>
            <Parallax className="custom-class" y={[-40, 40]} tagOuter="figure">
                <HomeComponent />
            </Parallax>
            <ServiciosComponent />
            <ClientesComponent />
        </div> 
    );
}
 
export default Home;